@import "./_variables";
@import "./reset";

// @font-face
//   font-family: Calibri
//   font-display: swap
//   font-style: normal
//   font-weight: normal
//   src: url("/fonts/Calibri/Calibri.ttf")

* {
  font-size: 18px;
  color: #000;
  font-family: Calibri;
  line-height: 22px;
  @media (min-width: 1800px) {
    font-size: 24px;
    line-height: 28px; } }

body {
  background-color: #ECECEC;
  text-align: center;
  position: relative;
  overflow-x: hidden;

  @media (min-width: 1800px) {
    font-size: 22px; } }

.page-container {
  display: inline-block;
  width: 1300px;
  margin: 0 auto;
  text-align: center;

  @media (min-width: 1800px) {
    width: 1700px; }

  @media (max-width: 1300px) {
    width: 1000px; }

  @media (max-width: 1024px) {
    width: 95%; } }

.content-container {
  padding: 20px; }

.overlay {
  background-color: rgba(0,0,0,0.3);
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
  z-index: 10;
  display: none;
  user-select: none;
  opacity: 0; }

@keyframes pulse {
  from {
    transform: scale(1.05); }
  50% {
    transform: scale(1); }
  to {
    transform: scale(1.05); } }
@keyframes fade-in {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

.button {
  color: white;
  display: inline-block;
  border-radius: 10px;
  padding: 10px 20px;
  background-color: $brand-color1;
  transition: 0.5s;
  font-family: Calibri;
  vertical-align: middle;
  cursor: pointer;
  @media (min-width: 1800px) {
    font-size: 24px; }
  p {
    font-family: Calibri;
    vertical-align: middle;
    color: white;
    display: inline-block;
    @media (min-width: 1800px) {
      font-size: 24px; } }
  img {
    width: 20px;
    heihgt: 20px;
    vertical-align: middle;
    display: inline-block; }
  &:hover {
    box-shadow: 0 0 5px rgba(0,0,0,0.5);
    transform: scale(1.1); } }

.switch-btn {
  display: block;
  width: 60px;
  height: 30px;
  border-radius: 15px;
  background: lightgray;
  z-index: 0;
  margin: 0;
  margin-top: 10px;
  padding: 0;
  border: none;
  cursor: pointer;
  position: relative;
  transition-duration: 300ms;
  p.switch-btn-notes {
    position: absolute;
    color: white;
    font-size: 12px;
    line-height: 12px;
    &.true {
      left: 12px;
      top: 8px; }
    &.false {
      left: 32px;
      top: 9px; } } }
.switch-btn::after {
  content: "";
  height: 24px;
  width: 24px;
  border-radius: 18px;
  background: #fff;
  top: 3px;
  left: 3px;
  transition-duration: 300ms;
  box-shadow: 0px 0px 10px rgba(0,0,0,0.2);
  position: absolute;
  z-index: 1; }
.switch-on {
  background: $brand-color1; }
.switch-on::after {
  left: 32px;
  background: white; }

.app-wrapper {
  display: grid;
  grid-template-rows: 60x 1fr;
  grid-template-columns: 2fr 10fr;
  grid-gap: 10px;
  grid-template-areas: "h h h" "n c c";
  width: 1200px;
  margin: 0 auto;
  text-align: left; }

.app-wrapper-content {
  grid-area: c;
  background-color: white; }
